import React, { useEffect, useRef, useState } from "react";
import styled from "styled-components";
import Selection from "@simonwep/selection-js";
import Draggable from "react-draggable";
import Word from "../Components/Word";
import Compress from "../Components/Compress";
import { news } from "../data";

const Wrapper = styled.div`
  margin: 0 auto;
  width: 100%;
  min-height: 80vh;
  padding: 0 0;
  display: flex;
  justify-content: space-around;
  align-items: center;
  line-height: 2rem;
`;

const Text = styled.div`
  width: 50%;
  height: calc(100vh - 3rem - 10px);
  overflow: scroll;
  user-select: none;
  padding: 2rem;
  &::-webkit-scrollbar {
    display: none;
  }
`;

const TextAreaWrapper = styled.div`
  width: 50%;
  height: calc(100vh - 3rem - 10px);
  padding: 2rem;
`;

const TextArea = styled.textarea`
  width: 100%;
  height: 100%;
  padding: 2rem;
  font-size: 0.8rem;
  font-family: "Nanum Myeongjo", serif;
  overflow: scroll;
  &::-webkit-scrollbar {
    display: none;
  }
`;

const Button = styled.div`
  width: 80%;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 10px;
  font-size: 0.9rem;
  &:hover {
    cursor: pointer;
  }
  &:active {
    border-radius: 0px;
    background: #ffffff;
    box-shadow: inset 5px 5px 10px #e6e6e6, inset -5px -5px 10px #ffffff;
  }
`;

const Create = () => {
  const data = news;
  const [text, setText] = useState([]);
  const target = useRef();

  useEffect(() => {
    // init
    let idx = 0;
    const newText = data[idx].split(/\s/g).map((item, idx) => [item, 0]);
    setText(newText);
  }, [data]);

  return (
    <Wrapper>
      <Text>
        {text.map((item, idx) => {
          if (item[0] === "<br/>") {
            return <br key={idx} id={idx} className="word" />;
          } else {
            return <Word key={idx} id={idx} text={item[0]} score={item[1]} />;
          }
        })}
      </Text>
      <TextAreaWrapper>
        <TextArea></TextArea>
      </TextAreaWrapper>
    </Wrapper>
  );
};

export default Create;
